import React from 'react'
import { graphql } from 'gatsby'

import SEO from '~/components/seo'
import ProductForm from '~/components/ProductForm'
import {
  Img,
  TwoColumnGrid,
  GridLeft,
  GridRight,
} from '~/utils/styles'
import Tabs from 'react-bootstrap/Tabs'
import Tab from 'react-bootstrap/Tab'

import {Container, Row, Col} from 'react-bootstrap'
import {
  ProductTitle,
  ProductDescription
} from './styles'

//TODO: Change to ProductCarousel and have product images as carousel indicators
import Carousel from 'react-bootstrap/Carousel'
//import ProductCarousel from '~/components/ProductCarousel'


const ProductPage = ({ data }) => {
  const product = data.shopifyProduct

  var spectableIndex = product.metafields.findIndex(function(metafield, index) {
    if (metafield.key == 'spectable') {
      return true;
    }
  })


  if (product.metafields[spectableIndex]) {
    var spectable = product.metafields[spectableIndex].value
  } else {
    var spectable = ''
  }

  const descriptionItems = product.descriptionHtml.split("<h6>");

  return (
    <>
      <SEO title={product.title} description={product.description} />
      <Container>
        <TwoColumnGrid className={'p-5'}>
          <GridLeft>
            <Carousel
                controls={true}
            >
              {product.images.map(image => (
                  <Carousel.Item>
                    <Img
                        fluid={image.localFile.childImageSharp.fluid}
                        key={image.id}
                        alt={product.title}
                    />
                  </Carousel.Item>
              ))}

            </Carousel>
          </GridLeft>
          <GridRight
            className="text-center">
            <ProductTitle>{product.title}</ProductTitle>

            <ProductForm product={product} />
          </GridRight>
        </TwoColumnGrid>
        <Row>
          <Col>
            <Tabs defaultActiveKey="specs" id="uncontrolled-tab-example" >
              <Tab eventKey="specs" title="Specifications" dangerouslySetInnerHTML={{__html: spectable}}>
              </Tab>
              {descriptionItems.slice(1).map(text => (
                  <Tab eventKey="name" title="Downloads" dangerouslySetInnerHTML={{__html: text}}>
                  </Tab>
              ))}
            </Tabs>
          </Col>
        </Row>
      </Container>
    </>
  )
}

export const query = graphql`
  query($handle: String!) {
    shopifyProduct(handle: { eq: $handle }) {
      id
      title
      handle
      productType
      description
      descriptionHtml
      shopifyId
      options {
        id
        name
        values
      }
      variants {
        id
        title
        price
        availableForSale
        shopifyId
        selectedOptions {
          name
          value
        }
      }
      priceRange {
        minVariantPrice {
          amount
          currencyCode
        }
        maxVariantPrice {
          amount
          currencyCode
        }
      }
      images {
        originalSrc
        id
        localFile {
          childImageSharp {
            fluid(maxWidth: 910) {
              ...GatsbyImageSharpFluid_withWebp_tracedSVG
            }
          }
        }
      }
      metafields {
        key
        value
      }
    }
  }
`

export default ProductPage


